@import url('https://fonts.googleapis.com/css2?family=Inter:wght@200&display=swap');

:root {
    --backgound-color: #21222a;
    --text-title-color: #deebf8;
    --header-text-color: #61dafb;
    --main-background-color: #282d35;
    --text-color: #fff;
    --nav-height: 16.5vh;
}

*, *::after, *::before {
    box-sizing: border-box;
    margin: 0;
    font-family: 'Inter', sans-serif;
}

.container {
    height: 100vh;
}

nav {
    background-color: var(--backgound-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3em;
    height: var(--nav-height);
}

.nav-icon {
    width: 3em;
}

.logo {
    display: flex;
    color: var(--header-text-color);
    align-items: center;
    column-gap: 1em;
}

.nav-logoText {
    font-size: 2em;
}

.nav-title {
    color: var(--text-title-color);
    font-size: 1.5em;
}

.main-content {
    background-color: var(--main-background-color);
    color: var(--text-color);
    height: calc(100vh - var(--nav-height));
    padding: 5em;
}

.main-title {
    font-size: 3.5em;
}

ul {
    list-style: none;
    line-height: 2.5;
    font-size: 1.5em;
    padding-top: 1.5em;
    width: 50vw;
}

ul li::before {
    content: "\2022";
    color: var(--header-text-color);
    font-weight: bold;
    display: inline-block;
    width: 1em;
    margin-left: -1em;
}